import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;

export default {
	data() {
		return {
			token: uni.getStorageSync('cdj_token'),
			imgRemote: imgRemote,
			showTop: false,
			kind: 0,
			navBar: navBar,
			loading:true,
			pbId: '',
			list: [],
			activeList: [],
			minute: 0,
			second: 0,
			config: [],
			goodIndex: '',
			arrObj: {},
			count:0
		};
	},
	methods: {
		closeKey(){
			this.$refs.keyboard.cancel();
		},
		leftClick() {




			uni.navigateBack({
				delta: 1
			});

		},
		detailPage(id) {
			this.count++;
			if (this.count != 1) return;
			setTimeout(() => {
				this.count = 0
			}, 1000)
			if (this.config.is_detail == 1) {
				uni.navigateTo({
					url: `shopdetail?id=${id}`
				})
			}
		},
		selectStage(index) {
			this.kind = index;
			this.pbId = this.activeList[index].id;
			this.loading=true;
			this.panicBuylist();
		},
		panicBuylist() {
			this.list=[];
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};
			let {
				pbId
			} = this;
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign,
					pbId: pbId
				},
				obj
			);
			rs.getRequests('panicBuyList', params, res => {
				let {
					data
				} = res;
				if (data.code == 200) {
					this.config = data.data;
					this.activeList = data.data.activeList;
					this.list = data.data.goodsList;
					this.loading=false;
				}
			});
		},
		// 显示键盘
		showKey(item, index) {
			this.$refs.popup.open();
			this.arrObj = item;
			this.goodIndex = index;
		},
		toParent(e) {
			this.addCart(this.arrObj, 'changeNum', e.val, '成功加入购物车', this.goodIndex);
			this.$refs.popup.close();
		},
		addCart(item, url, num, message, index) {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				item_id: item.item_id,
				attr_id: item.attr_id,
				item_num: num
			};

			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
					sign: sign
				},
				obj
			);
			rs.postRequests(url, params, res => {
				let data = res.data;
				if (data.code == 200) {
					rs.Toast(message);
					this.list[index].cart_num = num;
				} else if (data.code == 407 || data.code == 406) {
					rs.Toast("购买数量不能超过活动数量");
				} else {
					rs.Toast(res.data.msg);
				}
			});
		},
		plusSing(item, index) {
			this.addCart(item, 'changeNum', 1, '成功加入购物车', index)
		},
		plus(item, num, index) {
			if (item.is_float == 1 && !Number.isInteger(Number(num))) {
				rs.Toast('购买数量不能为小数');
				return;
			}
			this.addCart(item, 'changeNum', num, '成功加入购物车', index)
		},
		minus(item, num, index) {
			if (item.is_float == 1 && !Number.isInteger(Number(num))) {
				rs.Toast('购买数量不能为小数');
				return;
			}
			if (num <= 0) {
				this.addCart(item, 'deleteCart', num, '删除商品成功', index);
			} else {
				this.addCart(item, 'changeNum', num, '成功加入购物车', index)
			}
		}
	},
	onShow() {
		this.loading=true;
		this.panicBuylist();
	},
	onPageScroll(e) {
		if (e.scrollTop == 0) {
			this.showTop = false;
		} else {
			this.showTop = true;
		}
	}
};
